








import { Component, Prop, Vue } from "vue-property-decorator";
import { isNullOrUndefined } from "util";

@Component
export default class TimeFrame extends Vue {
  @Prop({ default: null })
  start!: Date;
  @Prop({ default: null })
  end!: Date;

  get computedEndDate(): String {
    if (isNullOrUndefined(this.end)) {
      return "Present";
    } else {
      let month = this.getDateMonth(this.end);
      let year = this.getDateYear(this.end);
      return `${month} ${typeof year === "number" ? String(year) : "Error"}`;
    }
  }

  get computedStartDate(): String {
    if (isNullOrUndefined(this.start)) {
      return "Unknown";
    } else {
      let month = this.getDateMonth(this.start);
      let year = this.getDateYear(this.start);
      return `${month} ${typeof year === "number" ? String(year) : "Error"}`;
    }
  }

  getDateYear(date: Date): Number | null {
    if (isNullOrUndefined(date)) {
      return null;
    } else {
      return date.getFullYear();
    }
  }

  getDateMonth(date: Date): String | null {
    if (isNullOrUndefined(date)) {
      return null;
    } else {
      return [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec"
      ][date.getMonth()];
    }
  }
}
